.container {
  max-width: 1140px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.header {
  position: fixed;
  top: 25px;
  left: 0;
  right: 0;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__logo {
  transition: top .5s .2s;
  position: relative;
}

.nav {
  position: relative;
}

.nav__menu {
  z-index: 5;
  background-color: #eef0e7;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 30px;
  padding: 50px;
  animation: hideMobile .5s;
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav__menu.active {
  animation: showMobile .5s;
}

.menu__item {
  font-size: 26px;
  font-weight: 500;
}

.menu__link {
  color: #000;
  transition: color .3s;
}

.menu__link:hover {
  color: #ffd723;
}

.open {
  display: flex;
}

.burger-container {
  width: 42px;
  height: 42px;
  cursor: pointer;
  z-index: 10;
  background-color: #eef0e7;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  padding: 14px 11px;
  animation: hideBorder .5s;
  display: flex;
  position: relative;
}

.burger-container.active {
  border: 1px solid #000;
  transition: transform .5s;
  animation: showBorder .5s;
}

.burger-container.active:hover {
  transform: scale(.9)rotate(1turn);
}

.burger {
  width: 18px;
  height: 12.6px;
  padding: 0;
  position: relative;
}

.burger .burger__stick {
  height: 2px;
  width: 100%;
  background-color: #000;
  transition: transform .3s;
  display: block;
}

.burger.active .burger__stick {
  transform: scale(0);
}

.burger:before, .burger:after {
  content: "";
  height: 2px;
  width: 100%;
  background-color: #000;
  transition: transform .3s;
  display: block;
  position: absolute;
}

.burger:before {
  top: 0;
}

.burger:after {
  bottom: 0;
}

.burger.active:before {
  top: 50%;
  transform: rotate(45deg)translateY(-50%);
}

.burger.active:after {
  bottom: 50%;
  transform: rotate(-45deg)translateY(50%);
}

@media (min-width: 400px) {
  .logo__img {
    content: url("logo.8f142ad4.svg");
  }
}

@media (min-width: 768px) {
  .burger-container {
    width: 40px;
    height: 40px;
    border: none;
    animation: none;
  }

  .burger-container.active {
    border: none;
    animation: none;
  }

  .burger-container.active:hover {
    transform: none;
  }

  .nav__menu {
    inset: inherit;
    width: 250px;
    background-color: #eef0e7;
    border-radius: 30px;
    padding: 30px 50px 30px 30px;
    animation: hideDesctop .6s;
    position: absolute;
    top: -3px;
    right: -3px;
    transform: translateY(-120%);
  }

  .nav__menu.active {
    animation: showDesctop .6s;
    transform: translateY(0);
  }

  .menu__item {
    font-size: 22px;
  }

  .menu__link {
    color: #000;
  }

  .menu__link:hover {
    color: #ffd723;
  }
}

@keyframes showDesctop {
  from {
    display: none;
    transform: translateY(-120%);
  }

  to {
    display: flex;
    transform: translateY(0);
  }
}

@keyframes hideDesctop {
  from {
    display: flex;
    transform: translateY(0);
  }

  to {
    display: none;
    transform: translateY(-120%);
  }
}

@keyframes showMobile {
  from {
    opacity: 0;
    display: none;
  }

  to {
    opacity: 1;
    display: flex;
  }
}

@keyframes hideMobile {
  from {
    opacity: 1;
    display: flex;
  }

  to {
    opacity: 0;
    display: none;
  }
}

@keyframes showBorder {
  from {
    border: 1px solid rgba(0, 0, 0, 0);
  }

  to {
    border: 1px solid #000;
  }
}

@keyframes hideBorder {
  from {
    border: 1px solid #000;
  }

  to {
    border: 1px solid rgba(0, 0, 0, 0);
  }
}

.overlay {
  -webkit-backdrop-filter: blur(2px);
  backdrop-filter: blur(2px);
  pointer-events: none;
  z-index: 15;
  background-color: rgba(0, 0, 0, .6);
  justify-content: center;
  align-items: center;
  animation: hide 2.5s linear forwards;
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.overlay:after {
  content: "";
  width: 300px;
  height: 100px;
  background-image: url("logo.8f142ad4.svg");
  background-repeat: no-repeat;
  background-size: contain;
  animation: show .5s;
  display: block;
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, 50%);
}

.loader {
  justify-content: space-between;
  align-items: center;
  gap: 25px;
  animation: show .5s;
  display: flex;
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, 50%);
}

.dot {
  width: 18px;
  height: 18px;
  background-color: #ffd723;
  border-radius: 50%;
  animation: bounce .6s infinite alternate;
}

.dot:nth-child(2) {
  animation-delay: .2s;
}

.dot:nth-child(3) {
  animation-delay: .4s;
}

@keyframes bounce {
  0% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(-15px);
  }
}

@keyframes hide {
  0% {
    -webkit-backdrop-filter: blur(2px);
    backdrop-filter: blur(2px);
    display: block;
  }

  60% {
    opacity: 1;
  }

  100% {
    -webkit-backdrop-filter: blur();
    backdrop-filter: blur();
    opacity: 0;
    display: none;
  }
}

@keyframes show {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@media (min-width: 480px) {
  .overlay:after {
    width: 400px;
